import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'master-select-demo-valueField',
  template: `
    <p> valueField="name"为返回师傅名称</p><br>
    <master-select name="masterName" width="150px" defaultLabel="请选择…" valueField="name"  [(ngModel)]="masterName"></master-select>
    <span>{{masterName}}</span>

    <p style="margin-top: 10px;"> valueField="mobile"为返回师傅电话</p><br>
    <master-select name="masterMobile" width="150px" defaultLabel="请选择…" valueField="mobile" [(ngModel)]="masterMobile"></master-select>
    <span>{{masterMobile}}</span>
  `,
  styles: []
})
export class MasterSelectDemoValueFieldComponent implements OnInit {

  masterName: string;
  masterMobile: string;

  constructor() { }

  ngOnInit() {
  }

}



