import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'master-select-demo-basic',
  template: `
    <master-select name="masterObj" width="150px" defaultLabel="请选择…" [(ngModel)]="masterObj"></master-select>
    <button (click)="domasterTest()">测试取值</button>
    <span>{{showObj}}</span>
  `,
  styles  : []
})
export class MasterSelectDemoBasicComponent implements OnInit {

  masterObj: any;
  showObj: any

  constructor() { }

  ngOnInit() {
  }

  domasterTest(){
    this.showObj = JSON.stringify(this.masterObj);
  }
  

}



