import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { MasterSelectDemoRoutingModule } from "app/showcase/custom-components/master-select-demo/master-select-demo.routing.module";
import { MasterSelectModule } from "app/custom-components/custom-components.module";
import { MasterSelectDemoComponent } from "app/showcase/custom-components/master-select-demo/master-select-demo.component";
import { MasterSelectDemoBasicComponent } from "app/showcase/custom-components/master-select-demo/master-select-demo-basic.component";
import { MasterSelectDemoTypeComponent } from "app/showcase/custom-components/master-select-demo/master-select-demo-type.component";
import { MasterSelectDemoValueFieldComponent } from "app/showcase/custom-components/master-select-demo/master-select-demo-valueField.component";

@NgModule({
	declarations:[
		MasterSelectDemoComponent,
		MasterSelectDemoBasicComponent,
		MasterSelectDemoTypeComponent,
		MasterSelectDemoValueFieldComponent
	],
	imports: [
		MasterSelectDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		MasterSelectModule
	]
})
export class MasterSelectDemoModule { }



